#price-spinner-overlay {
	position: fixed;
	top: 0; left: 0;
	width: 100%;
	height: 100%;
	background: rgba(75, 72, 82, 0.9);
	display: none;
	z-index: 9999;
}
.spinner-wrapper {
	width: 100px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
body {
    padding: 0;
    margin: 0;
}